import React from "react";
import s from "./ProfileInfo.module.css";
import userPhoto from "../../../images/images.png";
import Preloader from "../../common/loader/preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
class ProfileInfo extends React.Component {
 
  handleUploadPhoto = (e) => {
    if(e.target.files.length){
      this.props.savePhotoThunk(e.target.files[0])
    }
    
  }

  render() {
    if (!this.props.profile) {
      return <Preloader />;
    }
    return (
      <div className={s.box}>
        <div className={s.avatar}>
          <img src={this.props.profile.photos.large || userPhoto } className={s.mainPhoto} />
          {this.props.isOwner && <input type="file"  onChange={this.handleUploadPhoto} /> }
        </div>
        <div className={s.description}>
          <p>
            <b> userId: </b> {this.props.profile.userId}{" "}
          </p>
          <p>
            <b> fullname: </b> {this.props.profile.fullName}{" "}
          </p>
          <p>
            <b> status: </b> {this.props.profile.aboutMe}{" "}
          </p>
          <p>
            <b> facebook: </b> {this.props.profile.contacts.facebook}
          </p>
          <p>
            <b> github: </b> {this.props.profile.contacts.github}
          </p>
         <ProfileStatusWithHooks {...this.props} status={this.props.status} updateStatus={this.props.updateStatus}/>

        </div>
           </div>
    );
  }
}

export default ProfileInfo;
