import React from 'react'
import s from './post.module.css'

const Post = ({message,like}) =>{
    return(
        <div className={s.item}>
            <img src="https://img.icons8.com/material/4ac144/256/user-male.png" />
            {message}
            <div><span>Like</span> {like}</div>
        </div>
    )
}

export default Post
