import React, {  PureComponent } from 'react'
import s from './myPosts.module.css'
import Post from './Post/post'
import { reduxForm } from 'redux-form';
import { required, MaxLengthFuncCreator } from '../../../Utils/Validator/validator';
import { Textarea, TwinsField } from '../../common/loader/formsControls';

let maxLength10 = MaxLengthFuncCreator(10)
class MyPostsForm extends PureComponent  {
    render(){
    return(
        <form onSubmit={this.props.handleSubmit} className={s.box}>
                <div>
                  
                    {TwinsField("Post message","post",Textarea,[required,maxLength10],null,null)}
                </div>
                <div><button>Add post</button></div>
        </form>
    )
}
}

let MyPostsReduxForm = reduxForm({form: "myPost"})(MyPostsForm)


const MyPosts = ({addPost, message}) =>{
    let addPostSubmit = (formData) => {
        addPost(formData.post)
    }
    let messageBuilder = message.map( m => <Post message={m.mess} like={m.like} /> )
    return(
        <div className={s.myPosts}>
           <h2> My Posts </h2>
            <MyPostsReduxForm  onSubmit={addPostSubmit}/>
            <div className={s.posts}>
                { messageBuilder }
            </div>
        </div>
    )
}
export default MyPosts
