import React from "react";
import s from "./formsControls.module.css";
import { Field } from "redux-form";
export const FormControls = ({ input, meta, ...props }) => {
  return (
    <div>
      {meta.touched && meta.error && (
        <div className={`alert-danger p-2 mb-1 ${s.item}`}>
          <span>{meta.error}</span>
        </div>
      )}
      <div
        className={
          s.formControl + " " + (meta.touched && meta.error ? s.error : "")
        }
      >
        {props.children}
      </div>
    </div>
  );
};

export const Input = (props) => {
    let {input, meta, ...restProps} = props
  return (
    <FormControls {...props}>
      <input {...restProps} {...input} />
    </FormControls>
  );
};
export const Textarea = (props) => {
    let {input, meta, ...restProps} = props
  return (
    <FormControls {...props}>
      <textarea {...restProps} {...input} />
    </FormControls>
  );
};

export const TwinsField = (placeholder,name,component,[],value,type) => {
  return(
    <Field placeholder={placeholder} name={name} component={component} validate={[]} value={value} type={type} />
  )
}
